body {
    background-color: black;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    zoom: 3.5;
}

.modal {
    width: 60%;
    height: 60%;
}

.modal-content {
    color: blueviolet;
    font-size: 8px;
    border: 1px solid ivory;
}

span:hover {
    background-color: ivory;
}

h1 {
    color: blueviolet;
    text-align:center;
}

#piano {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 10px;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

.white {
    height: 95px;
    width: 20px;
    border: 1px solid;
    background-color: ivory;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.black {
    height: 56px;
    width: 15px;
    color: ivory;
    background-color: black;
    margin-left: -7.5px;
    margin-right: -7.5px;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.key:hover {
    background-color: gray;
}

