// need to implement

.slide-container {
    color: blueviolet;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 8px;
    display: flex;
    justify-content: center;
}

input {
    zoom: .4;
}

.slider {
    appearance: none;
    width: 60%;
    height: 10px;
    border-radius: 5px;  
    background: gray;
  }
  
  .slider::-webkit-slider-thumb {
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: blueviolet;
  }

  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: blueviolet;
  }

  

